@import url('https://fonts.googleapis.com/css2?family=Open+Sans:ital,wght@0,300;0,400;0,500;0,600;0,700;0,800;1,300;1,400;1,500;1,600&family=Playfair+Display:wght@400;500;600&display=swap');

:root{
  --slider-section-height: 600px;
  --feature-section-height: 710px;
}

*{
  box-sizing: border-box;
  padding: 0;
  margin: 0;
  font-family: 'Open Sans', sans-serif;
}

*::selection{
  background-color: #333333;
  color: white;
}

ul{
  list-style: none;
}

.container{
  width: 100%;
  position: relative;
}

.body{
  position: absolute;
  top: 600px;
  width: 100%;
  overflow: hidden;
}
